<template>
  <div>
    <!-- 面包屑 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>商品管理</el-breadcrumb-item>
      <el-breadcrumb-item>分类参数</el-breadcrumb-item>
    </el-breadcrumb>
    <!-- 卡片区域 -->
    <el-card class="box-card">
      <el-alert
        title="注意: 只允许为第三级分类设置相关参数!"
        type="warning"
        :closable="false"
        show-icon
      >
      </el-alert>
      <el-row>
        <el-col>
          <span>选择商品分类: </span>
          <el-cascader
            :options="all_CateList"
            :props="cascaderProps"
            v-model="cascader_value"
            @change="cascader_change"
          ></el-cascader
        ></el-col>
      </el-row>
      <!--  -->
      <el-tabs v-model="tabname" @tab-click="handleClick">
        <el-tab-pane label="动态参数" name="many">
          <el-button
            type="primary"
            size="mini"
            :disabled="this.is_btn"
            @click="add_dialog_show"
          >
            添加参数
          </el-button>
          <el-table :data="param_target" border stripe>
            <!-- 展开列 -->
            <el-table-column type="expand">
              <template slot-scope="scope">
                <el-tag
                  v-for="(tag, index) in get_attr_vals(scope.row.attr_vals)"
                  :key="index"
                  closable
                  @close="tag_close(scope.row, index)"
                >
                  {{ tag }}
                </el-tag>

                <el-input
                  class="input-new-tag"
                  v-if="inputVisible"
                  v-model="inputValue"
                  ref="saveTagInput"
                  size="small"
                  @keyup.enter.native="handleInputConfirm(scope.row)"
                  @blur="handleInputConfirm(scope.row)"
                >
                </el-input>
                <el-button
                  v-else
                  class="button-new-tag"
                  size="small"
                  @click="showInput"
                >
                  + New Tag
                </el-button>
              </template>
            </el-table-column>
            <!-- 索引列 -->
            <el-table-column type="index" label="#"> </el-table-column>
            <el-table-column label="参数名称" prop="attr_name">
            </el-table-column>
            <el-table-column label="操作">
              <template slot-scope="scope">
                <el-button
                  type="primary"
                  icon="el-icon-edit"
                  size="mini"
                  @click="edit_dialog_show(scope.row)"
                  >编辑</el-button
                >
                <el-button
                  type="danger"
                  icon="el-icon-delete"
                  size="mini"
                  @click="remove_dialog_by(scope.row.attr_id)"
                  >删除</el-button
                >
              </template>
            </el-table-column>
          </el-table>
        </el-tab-pane>
        <el-tab-pane label="静态属性" name="only">
          <el-button
            type="primary"
            size="mini"
            :disabled="is_btn"
            @click="add_dialog_show"
          >
            添加属性
          </el-button>
          <el-table :data="param_target" border stripe>
            <!-- 展开列 -->
            <el-table-column type="expand">
              <template slot-scope="scope">
                <el-tag
                  v-for="(tag, index) in get_attr_vals(scope.row.attr_vals)"
                  :key="index"
                  closable
                  @close="tag_close(scope.row, index)"
                >
                  {{ tag }}
                </el-tag>
                <el-input
                  class="input-new-tag"
                  v-if="inputVisible"
                  v-model="inputValue"
                  ref="saveTagInput"
                  size="small"
                  @keyup.enter.native="handleInputConfirm(scope.row)"
                  @blur="handleInputConfirm(scope.row)"
                >
                </el-input>
                <el-button
                  v-else
                  class="button-new-tag"
                  size="small"
                  @click="showInput"
                >
                  + New Tag
                </el-button>
              </template>
            </el-table-column>
            <!-- 索引列 -->
            <el-table-column type="index" label="#"> </el-table-column>
            <el-table-column label="参数名称" prop="attr_name">
            </el-table-column>
            <el-table-column label="操作">
              <template slot-scope="scope">
                <el-button
                  type="primary"
                  icon="el-icon-edit"
                  size="mini"
                  @click="edit_dialog_show(scope.row)"
                  >编辑</el-button
                >
                <el-button
                  type="danger"
                  icon="el-icon-delete"
                  size="mini"
                  @click="remove_dialog_by(scope.row.attr_id)"
                  >删除</el-button
                >
              </template>
            </el-table-column>
          </el-table>
        </el-tab-pane>
      </el-tabs>
    </el-card>

    <!-- 对话框 -->
    <el-dialog
      :title="'添加' + title_txt"
      :visible.sync="addDialogVisible"
      width="50%"
      @close="add_dialog_close"
    >
      <el-form
        ref="addFormRef"
        label-width="100px"
        :model="addForm"
        :rules="addFormRules"
      >
        <el-form-item :label="title_txt" prop="attr_name">
          <el-input v-model="addForm.attr_name"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="add_dialog_post">确 定</el-button>
      </span>
    </el-dialog>

    <el-dialog
      :title="'编辑' + title_txt"
      :visible.sync="editDialogVisible"
      width="50%"
      @close="edit_dialog_close"
    >
      <el-form
        ref="editFormRef"
        label-width="100px"
        :model="editForm"
        :rules="editFormRules"
      >
        <el-form-item :label="title_txt" prop="attr_name">
          <el-input v-model="editForm.attr_name"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="edit_dialog_put">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      // #region
      // #endregion
      // 选择器
      // #region

      all_CateList: [],
      cascaderProps: {
        label: 'cat_name',
        value: 'cat_id',
        children: 'children',
        expandTrigger: 'hover'
      },
      cascader_value: [],
      tabname: 'many',
      // #endregion
      // 参数
      // #region
      param_target: [],

      // #endregion

      // 添加参数对话框
      // #region
      addDialogVisible: false,
      addForm: {
        attr_name: ''
      },
      addFormRules: {
        attr_name: [
          { required: true, message: '请输入参数名', trigger: 'blur' },
          { min: 1, max: 10, message: '长度在 1 到 10 个字符', trigger: 'blur' }
        ]
      },
      editDialogVisible: false,
      editForm: {
        attr_name: ''
      },
      editFormRules: {
        attr_name: [
          { required: true, message: '请输入参数名', trigger: 'blur' },
          { min: 1, max: 10, message: '长度在 1 到 10 个字符', trigger: 'blur' }
        ]
      },

      // #endregion
      // #region
      inputVisible: false,
      inputValue: ''
      // #endregion
    }
  },
  methods: {
    // 获取数据
    // #region
    async get_all_CateList() {
      const { data: res } = await this.$http.get('categories')
      if (res.meta.status !== 200) {
        return this.$message.error('获取商品列表失败')
      }
      this.$message.success('获取商品列表成功')
      this.all_CateList = res.data
      // console.log(this.all_CateList)
    },
    cascader_change() {
      if (this.cascader_value.length !== 3) {
        this.cascader_value = []
        return this.$message.error('不是三级分类，请重新选择')
      }
      this.get_attributes()
      // console.log(this.cascader_value)
    },
    // #endregion

    // 展示模块
    // #region
    handleClick() {
      this.get_attributes()
    },
    async get_attributes() {
      // console.log(this.cate_id)
      const { data: res } = await this.$http.get(
        'categories/' + this.cate_id + '/attributes',
        {
          params: { sel: this.tabname }
        }
      )
      if (res.meta.status !== 200) {
        return this.$message.error('获取参数失败')
      }
      this.$message.success('获取参数成功')
      // console.log(res.data[0].attr_name)
      // console.log(res.data)
      this.param_target = res.data
    },
    get_attr_vals(value) {
      return value !== '' ? value.trim().split(' ') : []
    },

    // #endregion

    // 添加参数
    // #region
    add_dialog_show() {
      this.addDialogVisible = true
    },
    add_dialog_post() {
      // console.log(this.param_target)
      this.$refs.addFormRef.validate(async (valid) => {
        if (!valid) {
          return this.$message.error('输入正确参数')
        }
        const { data: res } = await this.$http.post(
          'categories/' + this.cate_id + '/attributes',
          {
            attr_name: this.addForm.attr_name,
            attr_sel: this.tabname
          }
        )
        if (res.meta.status !== 201) {
          return this.$message.error('添加失败')
        }
        this.$message.success('添加参数成功')
        this.get_attributes()
        this.addDialogVisible = false
      })
    },
    add_dialog_close() {
      this.$refs.addFormRef.resetFields()
    },
    // #endregion
    // 编辑参数
    // #region
    edit_dialog_show(value) {
      // console.log(value)
      this.editForm = value
      this.editDialogVisible = true
    },
    edit_dialog_put() {
      // console.log(this.editForm.attr_name)
      // console.log(this.editForm.attr_id)
      this.$refs.editFormRef.validate(async (valid) => {
        // console.log(valid)
        if (!valid) {
          return this.$message.error('输入正确参数')
        }
        const { data: res } = await this.$http.put(
          'categories/' + this.cate_id + '/attributes/' + this.editForm.attr_id,
          {
            attr_name: this.editForm.attr_name,
            attr_sel: this.tabname
          }
        )
        if (res.meta.status !== 200) {
          return this.$message.error('编辑失败')
        }
        // console.log(res)
        this.$message.success('编辑参数成功')

        this.get_attributes()
        this.editDialogVisible = false
      })
    },
    edit_dialog_close() {
      this.$refs.editFormRef.resetFields()
    }, // 移除权限
    // #endregion

    async remove_dialog_by(attrid) {
      // 弹窗提示

      const confirmResult = await this.$confirm(
        '此操作将永久删除, 是否继续?',
        '警告',
        {
          cancelButtonText: '取消',
          confirmButtonText: '确定',
          type: 'warning'
        }
      ).catch((err) => {
        return err
      })
      // 用户确认删除为 confirm
      if (confirmResult !== 'confirm') {
        return this.$message.info('取消删除')
      }
      const { data: res } = await this.$http.delete(
        'categories/' + this.cate_id + '/attributes/' + attrid
      )
      if (res.meta.status !== 200) {
        return this.$message.error('删除失败')
      }
      this.$message.success(res.meta.msg)
      this.get_attributes()
    },
    // 关闭参数标签
    async tag_close(row, index) {
      const confirmResult = await this.$confirm(
        '此操作将永久删除, 是否继续?',
        '警告',
        {
          cancelButtonText: '取消',
          confirmButtonText: '确定',
          type: 'warning'
        }
      ).catch((err) => {
        return err
      })
      // 用户确认删除为 confirm
      if (confirmResult !== 'confirm') {
        return this.$message.info('取消删除')
      }
      // console.log(row, index)
      const sl = this.get_attr_vals(row.attr_vals)
      sl.splice(index, 1)
      // console.log(sl)
      row.attr_vals = sl.join(' ')
      // console.log(row)

      const { data: res } = await this.$http.put(
        'categories/' + this.cate_id + '/attributes/' + row.attr_id,
        {
          attr_name: row.attr_name,
          attr_sel: this.tabname,
          attr_vals: row.attr_vals
        }
      )
      if (res.meta.status !== 200) {
        return this.$message.error('编辑失败')
      }
      // console.log(res)
      this.$message.success('编辑参数成功')
    },

    // 标签
    // #region

    showInput() {
      this.inputVisible = true
      this.$nextTick((_) => {
        this.$refs.saveTagInput.$refs.input.focus()
      })
    },
    async handleInputConfirm(row) {
      // console.log(row)
      if (this.inputValue !== '') {
        row.attr_vals = row.attr_vals + ' ' + this.inputValue
        this.inputValue = ''

        const { data: res } = await this.$http.put(
          'categories/' + this.cate_id + '/attributes/' + row.attr_id,
          {
            attr_name: row.attr_name,
            attr_sel: this.tabname,
            attr_vals: row.attr_vals
          }
        )
        if (res.meta.status !== 200) {
          return this.$message.error('编辑失败')
        }
        this.$message.success('编辑参数成功')
      }
      // console.log(res)

      // console.log(this.inputValue)

      this.inputVisible = false
    }
    // #endregion
  },
  created() {
    this.get_all_CateList()
  },
  computed: {
    is_btn() {
      if (this.cascader_value.length === 0) {
        return true
      } else {
        return false
      }
    },
    cate_id() {
      if (this.cascader_value.length === 3) {
        return this.cascader_value[2]
      }
      return null
    },
    title_txt() {
      if (this.tabname === 'many') {
        return '动态参数'
      } else {
        return '静态参数'
      }
    }
  }
}
</script>

<style scoped>
.el-row {
  margin-top: 15px;
}
.el-tag {
  margin-left: 15px;
}
.el-button {
  margin-left: 15px;
}
.button-new-tag {
  margin-left: 10px;
  height: 32px;
  line-height: 30px;
  padding-top: 0;
  padding-bottom: 0;
}
.input-new-tag {
  width: 90px;
  margin-left: 10px;
  vertical-align: bottom;
}
</style>
