/* eslint-disable space-before-function-paren */
<template>
  <div class="login_container">
    <div class="login_box">
      <!-- 头像 -->
      <div class="avator_box">
        <img src="../assets/logo.png" alt="" />
      </div>
      <!-- 表单 -->
      <el-form
        ref="LoginFormRef"
        :model="LoginForm"
        :rules="LoginFormRules"
        label-width="0"
        class="login_form"
      >
        <el-form-item label="" prop="username">
          <!-- el ui -->
          <el-input
            prefix-icon="el-icon-user-solid"
            v-model="LoginForm.username"
          ></el-input>
        </el-form-item>
        <el-form-item label="" prop="password">
          <!-- 阿里图标 -->
          <el-input
            prefix-icon="iconfont icon-3702mima"
            v-model="LoginForm.password"
            type="password"
          ></el-input>
        </el-form-item>
        <!-- 按钮 -->
        <el-form-item label=".." class="btnr">
          <el-button type="primary" @click="login_btn">登录</el-button>
          <!-- <el-button type="info" plain>重置</el-button> -->
          <el-button type="info" @click="resetLoginForm">重置</el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
export default {
  // eslint-disable-next-line space-before-function-paren
  data() {
    return {
      LoginForm: {
        username: 'admin',
        password: '123456'
      },
      LoginFormRules: {
        username: [
          { required: true, message: '请输入登录名称', trigger: 'blur' },
          { min: 3, max: 10, message: '长度在 3 到 10 个字符', trigger: 'blur' }
        ],
        password: [
          { required: true, message: '请输入登录密码', trigger: 'blur' },
          { min: 6, max: 15, message: '长度在 6 到 15 个字符', trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    // eslint-disable-next-line space-before-function-paren
    resetLoginForm() {
      // console.log(this)
      this.$refs.LoginFormRef.resetFields()
    },
    // eslint-disable-next-line space-before-function-paren
    login_btn() {
      // eslint-disable-next-line space-before-function-paren
      this.$refs.LoginFormRef.validate(async (valid) => {
        // console.log(valid) // 验证是否成功
        // eslint-disable-next-line no-useless-return
        if (!valid) return
        // var user = {
        //   username: 'admin',
        //   password: 123456
        //   password: '123456'
        // }
        // console.log(user)
        const { data: res } = await this.$http.post('login', this.LoginForm)
        // const { data: res } = await this.$http.post('login', user)
        // console.log(res)
        if (res.meta.status !== 200) {
          // return console.log('登录失败')
          return this.$message.error('登录失败')
        }

        this.$message.success('登录成功')
        window.sessionStorage.setItem('token', res.data.token)
        this.$router.push('/home')

        // if (res.meta.status !== 200) return this.$message.error('登录失败')
        // if (res.meta.status !== 200) {
        //   // console.log(res.meta.status)
        //   this.$message.error('错了哦，这是一条错误消息')
        // }
        // this.$message.error('错了哦，这是一条错误消息')
        // console.log(res.meta.status)

        // this.$message({
        //   message: '恭喜你，这是一条成功消息',
        //   type: 'success'
        // })

        // console.log('登录成功')
      })
    }
  }
}
</script>

<style  scoped>
.login_container {
  /* background-color: #2b4b6b; */
  /* background: linear-gradient(direction, #2b4b6b, #41b883); */
  background-image: linear-gradient(to bottom left, #2b4b6b, #41b883);

  height: 100%;
}
.login_box {
  width: 450px;
  height: 300px;
  background-color: #fff;
  border-radius: 3px;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}
.avator_box {
  height: 130px;
  width: 130px;
  border: 1px solid #eee;
  border-radius: 50%;
  padding: 10px;
  box-shadow: 0 0 10px #ddd;
  position: absolute;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: #fff;
}
.avator_box img {
  width: 100%;
  height: 100%;
  border-radius: 50%;
  background-color: #eee;
}
.login_form {
  box-sizing: border-box;
  position: absolute;
  bottom: 0;
  width: 100%;
  padding: 0 30px;
}
.btnr {
  display: flex;
  justify-content: flex-end;
}
</style>
