<template>
  <!-- 用户列表组件 -->
  <div>
    <!-- <h1>users</h1> -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>用户管理</el-breadcrumb-item>
      <el-breadcrumb-item>用户列表</el-breadcrumb-item>
    </el-breadcrumb>
    <el-card class="box-card">
      <!-- <div v-for="o in 4" :key="o" class="text item">
        {{ '列表内容 ' + o }}
      </div> -->
      <el-row :gutter="20">
        <el-col :span="9"
          ><el-input
            placeholder="请输入内容"
            v-model="queryInfo.query"
            clearable
            @clear="getUserList"
          >
            <el-button
              slot="append"
              icon="el-icon-search"
              @click="getUserList"
            ></el-button> </el-input
        ></el-col>
        <el-col :span="4">
          <el-button type="primary" @click="addDialogVisible = true">
            添加用户</el-button
          >
        </el-col>
      </el-row>
      <el-table :data="userList" border stripe>
        <el-table-column type="index" label="#"> </el-table-column>
        <el-table-column prop="username" label="姓名"> </el-table-column>
        <el-table-column prop="email" label="邮箱"> </el-table-column>
        <el-table-column prop="mobile" label="电话"> </el-table-column>
        <el-table-column prop="role_name" label="角色"> </el-table-column>
        <el-table-column label="状态">
          <template slot-scope="scope">
            <el-switch
              @change="userStateChaneged(scope.row)"
              v-model="scope.row.mg_state"
              active-color="#13ce66"
              inactive-color="#ff4949"
            >
            </el-switch>
            <!-- {{ scope.row }} -->
          </template>
        </el-table-column>
        <el-table-column label="操作">
          <!-- <template slot-scope="scope"> -->
          <template slot-scope="scope">
            <el-button
              type="primary"
              icon="el-icon-edit"
              size="mini"
              @click="showEditDialog(scope.row.id)"
            ></el-button>
            <el-button
              type="danger"
              icon="el-icon-delete"
              size="mini"
              @click="removeUserById(scope.row.id)"
            ></el-button>

            <el-tooltip
              effect="dark"
              content="分配角色"
              :enterable="false"
              placement="top"
            >
              <el-button
                type="warning"
                icon="el-icon-setting"
                size="mini"
                @click="setRoleDialog(scope.row)"
              >
              </el-button>
            </el-tooltip>
            <!-- {{ scope.row }} -->
          </template>
        </el-table-column>
      </el-table>
      <!-- 分页区域 -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[3, 4, 5]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      >
      </el-pagination>
    </el-card>
    <!-- 对话框 -->
    <el-dialog
      title="添加用户"
      :visible.sync="addDialogVisible"
      width="50%"
      @close="addDialogClosed"
    >
      <!-- <span>添加用户</span> -->
      <el-form
        :model="addForm"
        :rules="addFormRules"
        ref="addFormRef"
        label-width="70px"
      >
        <el-form-item label="用户名" prop="username">
          <el-input v-model="addForm.username"></el-input>
        </el-form-item>
        <el-form-item label="密码" prop="password">
          <el-input v-model="addForm.password"></el-input>
        </el-form-item>
        <el-form-item label="邮箱" prop="email">
          <el-input v-model="addForm.email"></el-input>
        </el-form-item>
        <el-form-item label="手机" prop="mobile">
          <el-input v-model="addForm.mobile"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addDialogVisible = false">取 消</el-button>
        <!-- <el-button type="primary" @click="addDialogVisible = false">
          确 定
        </el-button> -->
        <el-button type="primary" @click="addUser"> 确 定 </el-button>
      </span>
    </el-dialog>

    <!-- 编辑用户的对话框 -->
    <el-dialog
      title="编辑用户"
      ref="editFormRef"
      width="50%"
      :visible.sync="editDialogVisible"
      @close="editDialogClosed"
    >
      <el-form
        :model="editForm"
        label-width="70px"
        :rules="editFormRules"
        ref="editFormRef"
      >
        <el-form-item label="用户名">
          <el-input v-model="editForm.username" disabled></el-input>
        </el-form-item>
        <el-form-item label="邮箱" prop="email">
          <el-input v-model="editForm.email"></el-input>
        </el-form-item>
        <el-form-item label="手机" prop="mobile">
          <el-input v-model="editForm.mobile"></el-input>
        </el-form-item>
      </el-form>
      <!-- 底部 -->
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false">取 消</el-button>
        <!-- <el-button type="primary" @click="addDialogVisible = false">
          确 定
        </el-button> -->
        <el-button type="primary" @click="editUserInfo"> 确 定 </el-button>
      </span>
    </el-dialog>

    <!-- 分配角色 -->
    <el-dialog title="分配角色" width="50%" :visible.sync="setDialogVisible">
      <div>
        <p>当前用户: {{ roleInfo.username }}</p>
        <p>当前角色: {{ roleInfo.role_name }}</p>
        <p>
          分配角色:
          <el-select v-model="selectRoleId" placeholder="请选择">
            <el-option
              v-for="item in roleList"
              :key="item.id"
              :label="item.roleName"
              :value="item.id"
            >
            </el-option>
          </el-select>
        </p>
      </div>

      <!-- 底部 -->
      <span slot="footer" class="dialog-footer">
        <el-button @click="setDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="setRole"> 确 定 </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    // 自定义校验规则
    var checkEmail = (rule, value, cb) => {
      //   const regEmail = /^([a-zA-Z0-9_-])+@([a-zA-Z0-9_-])+(\.[a-zA-Z0-9_-])+/
      const regEmail = /^\w+@\w+(\.\w+)+$/ // 验证邮箱的正则表达式

      if (regEmail.test(value)) {
        // 合法
        return cb()
      }
      cb(new Error('请输入合法邮箱'))
    }
    var checkPhone = (rule, value, cb) => {
      //   const regPhone = /^(0|86|17951)?/
      const regPhone = /^1[34578]\d{9}$/

      if (regPhone.test(value)) {
        // 合法
        return cb()
      }
      cb(new Error('请输入合法手机号'))
    }

    return {
      // 分配角色对话框控制
      selectRoleId: '',
      setDialogVisible: false,
      roleList: [],

      // 获取参数对象
      queryInfo: {
        query: '',
        pagenum: 1,
        pagesize: 3
      },
      userList: [],
      total: 3,
      // 控制对话框
      addDialogVisible: false,
      // 编辑对话框显示
      editDialogVisible: false,
      // 编辑表单
      editForm: {},
      roleInfo: {},
      // 添加表单
      addForm: {
        username: '',
        password: '',
        email: '',
        mobile: ''
      },
      // 添加用户验证规则
      addFormRules: {
        username: [
          { required: true, message: '请输入用户名', trigger: 'blur' },
          { min: 3, max: 10, message: '长度在 3 到 10 个字符', trigger: 'blur' }
        ],
        password: [
          { required: true, message: '请输入密码', trigger: 'blur' },
          { min: 6, max: 15, message: '长度在 6 到 15 个字符', trigger: 'blur' }
        ],
        email: [
          { required: true, message: '请输入邮箱', trigger: 'blur' },
          { validator: checkEmail, trigger: 'blur' }
          //   { min: 6, max: 15, message: '长度在 6 到 15 个字符', trigger: 'blur' }
        ],
        mobile: [
          { required: true, message: '请输入手机号', trigger: 'blur' },
          { validator: checkPhone, trigger: 'blur' }

          //   { min: 11, max: 11, message: '请输入正确的手机号', trigger: 'blur' }
        ]
      },
      // 修改用户验证规则

      editFormRules: {
        email: [
          { required: true, message: '请输入邮箱', trigger: 'blur' },
          { validator: checkEmail, trigger: 'blur' }
          //   { min: 6, max: 15, message: '长度在 6 到 15 个字符', trigger: 'blur' }
        ],
        mobile: [
          { required: true, message: '请输入手机号', trigger: 'blur' },
          { validator: checkPhone, trigger: 'blur' }

          //   { min: 11, max: 11, message: '请输入正确的手机号', trigger: 'blur' }
        ]
      }
    }
  },
  created() {
    this.getUserList()
  },
  methods: {
    async getUserList() {
      //   const strl = await this.$http.get('users')
      const { data: res } = await this.$http.get('users', {
        params: this.queryInfo
      })
      //   console.log(res)
      if (res.meta.status !== 200) {
        return this.$message.error('获取用户列表失败')
      }
      this.$message.success('获取用户列表成功')
      this.userList = res.data.users
      this.total = res.data.total
      //   console.log(strl)
    },
    handleSizeChange(newSize) {
      this.queryInfo.pagesize = newSize
      this.getUserList()
    },
    handleCurrentChange(newPage) {
      this.queryInfo.pagenum = newPage
      this.getUserList()
    },
    async userStateChaneged(userinfo) {
      const { data: res } = await this.$http.put(
        `users/${userinfo.id}/state/${userinfo.mg_state}`
      )
      if (res.meta.status !== 200) {
        userinfo.mg_state = !userinfo.mg_state
        return this.$message.error('更新用户状态失败')
      }
      this.$message.success('更新用户状态成功')
    },
    addDialogClosed() {
      this.$refs.addFormRef.resetFields()
    },
    // 监听修改用户框的关闭事件
    editDialogClosed() {
      this.$refs.editFormRef.resetFields()
    },
    addUser() {
      this.$refs.addFormRef.validate(async (valid) => {
        // console.log(valid)
        if (!valid) {
          return this.$message.error('输入失败')
        }
        const { data: res } = await this.$http.post('users', this.addForm)
        if (res.meta.status !== 201) {
          return this.$message.error('添加用户失败')
        }
        this.$message.success('添加用户成功')
        this.addDialogVisible = false
        this.getUserList()
      })
    },
    async showEditDialog(id) {
      //   console.log(`id:${id}`)
      const { data: res } = await this.$http.get('users/' + id)
      if (res.meta.status !== 200) {
        return this.$message.error('编辑用户请求失败')
      }
      this.editForm = res.data

      this.editDialogVisible = true
    },
    // 修改用户信息并提交
    editUserInfo() {
      // 预验证
      this.$refs.editFormRef.validate(async (valid) => {
        // 预验证失败跳出
        if (!valid) {
          return this.$message.error('校验数据不通过')
        }
        // 发起修改数据请求
        const { data: res } = await this.$http.put(
          'users/' + this.editForm.id,
          {
            email: this.editForm.email,
            mobile: this.editForm.mobile
          }
        )
        // 更新失败
        if (res.meta.status !== 200) {
          return this.$message.error('更新失败')
        }
        // 关闭对话框
        this.editDialogVisible = false
        // 刷新列表
        this.getUserList()
        // 提示
        this.$message.success('更新成功')

        // console.log(res)
      })
    },
    // 移除用户信息
    async removeUserById(id) {
      // 弹窗提示
      const confirmResult = await this.$confirm(
        '此操作将永久删除, 是否继续?',
        '警告',
        {
          cancelButtonText: '取消',
          confirmButtonText: '确定',
          type: 'warning'
        }
      ).catch((err) => {
        // 取消删除 提示
        // cancel错误
        // this.$message.info('取消删除')
        return err
      })
      // 用户确认删除为 confirm
      //   console.log(confirmResult)
      if (confirmResult !== 'confirm') {
        return this.$message.info('取消删除')
      }
      // 删除用户
      const { data: res } = await this.$http.delete('users/' + id, { id: id })
      console.log(res)
      if (res.meta.status !== 200) {
        return this.$message.error('删除失败')
      }
      // 确定删除
      this.$message.success('成功删除')
      // 刷新列表
      this.getUserList()
    },
    // 分配角色设置
    async setRoleDialog(role) {
      this.roleInfo = role
      // 获取角色列表
      const { data: res } = await this.$http.get('roles/')
      if (res.meta.status !== 200) {
        return this.$message.error('获取列表失败')
      }
      this.$message.success(res.meta.msg)
      this.roleList = res.data

      this.setDialogVisible = true
    },
    async setRole() {
      // console.log(this.selectRoleId)
      if (this.selectRoleId === '') {
        return this.$message.error('请选择角色')
      }
      // console.log(this.roleInfo)
      // this.roleInfo.role_name = this.selectRoleId
      // console.log(this.username)
      console.log(this.selectRoleId)
      const { data: res } = await this.$http.put(
        'users/' + this.roleInfo.id + '/role',
        {
          rid: this.selectRoleId
        }
      )
      if (res.meta.status !== 200) {
        return this.$message.error('获取失败')
      }
      this.selectRoleId = []
      this.getUserList()
      this.$message.success(res.meta.msg)
      this.setDialogVisible = false
    }
  }
}
</script>

<style scoped>
</style>
