<template>
  <div>
    <!-- 面包屑 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>商品管理</el-breadcrumb-item>
      <el-breadcrumb-item>商品列表</el-breadcrumb-item>
    </el-breadcrumb>
    <!-- 卡片区域 -->
    <el-card class="box-card">
      <!--  -->
      <el-input placeholder="请输入内容" v-model="queryInfo.query" clearable>
        <el-button
          slot="append"
          icon="el-icon-search"
          @click="get_good_list"
        ></el-button>
      </el-input>
      <el-button type="primary" class="btn-left" @click="to_link_add_good"
        >添加商品</el-button
      >
      <!--  -->
      <el-table :data="good_list" border stripe>
        <el-table-column type="index" label="#"> </el-table-column>
        <el-table-column label="商品名称" prop="goods_name" width="550">
        </el-table-column>
        <el-table-column label="价格(元)" prop="goods_price"> </el-table-column>
        <el-table-column label="商品重量" prop="goods_weight">
        </el-table-column>
        <el-table-column label="创建时间" prop="add_time" width="200">
          <template slot-scope="scope">
            {{ time_change(scope.row.add_time) }}
          </template>
        </el-table-column>
        <el-table-column label="操作">
          <template slot-scope="scope">
            <el-button
              type="primary"
              icon="el-icon-edit"
              size="mini"
              @click="edit_dialog_show(scope.row.goods_id)"
            ></el-button>
            <el-button
              type="danger"
              icon="el-icon-delete"
              size="mini"
              @click="remove_good_by_id(scope.row.goods_id)"
            ></el-button>
          </template>
        </el-table-column>
      </el-table>
      <!--  -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[3, 4, 5]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      >
      </el-pagination>
    </el-card>
    <!--  -->
    <el-dialog
      title="编辑商品信息"
      width="60%"
      :visible.sync="edit_visble"
      :model="editForm"
    >
      <el-form label-width="70px" ref="editFormRef">
        <el-form-item label="商品名称">
          <el-input
            v-model="editForm.goods_name"
            style="width: 600px"
          ></el-input>
        </el-form-item>
        <el-form-item label="价格" prop="goods_name">
          <el-input v-model="editForm.goods_price"></el-input>
        </el-form-item>
        <el-form-item label="数量" prop="goods_name">
          <el-input v-model="editForm.goods_number"></el-input>
        </el-form-item>
        <el-form-item label="重量" prop="goods_name">
          <el-input v-model="editForm.goods_weight"></el-input>
        </el-form-item>
      </el-form>
      <!-- 底部 -->
      <span slot="footer" class="dialog-footer">
        <el-button @click="edit_visble = false">取 消</el-button>

        <el-button type="primary" @click="edit_dialog_put"> 确 定 </el-button>
      </span>
    </el-dialog>
    <!--  -->
  </div>
</template>

<script>
// #
// #region
// #endregion
export default {
  data() {
    return {
      editForm: {},

      edit_visble: false,
      good_list: [],
      queryInfo: {
        query: '',
        pagenum: 1,
        pagesize: 5
      },
      total: 0
    }
  },
  created() {
    this.get_good_list()
  },
  methods: {
    async get_good_list() {
      const { data: res } = await this.$http.get('goods', {
        params: this.queryInfo
      })
      // console.log(res)
      if (res.meta.status !== 200) {
        return this.$message.error('获取商品列表失败')
      }
      this.$message.success('获取商品列表成功')
      this.good_list = res.data.goods
      this.total = res.data.total
      // console.log(this.good_list)
    },
    handleSizeChange(pagesize) {
      this.queryInfo.pagesize = pagesize
      this.get_good_list()
    },
    handleCurrentChange(pagenum) {
      this.queryInfo.pagenum = pagenum
      this.get_good_list()
    },
    async remove_good_by_id(id) {
      // console.log(id)
      // 弹窗提示
      const confirmResult = await this.$confirm(
        '此操作将永久删除, 是否继续?',
        '警告',
        {
          cancelButtonText: '取消',
          confirmButtonText: '确定',
          type: 'warning'
        }
      ).catch((err) => {
        return err
      })

      if (confirmResult !== 'confirm') {
        return this.$message.info('取消删除')
      }
      // 删除用户
      const { data: res } = await this.$http.delete('goods/' + id)
      // console.log(res)
      if (res.meta.status !== 200) {
        return this.$message.error('删除失败')
      }
      // 确定删除
      this.$message.success('成功删除')
      // 刷新列表
      this.get_good_list()
    },
    async edit_dialog_show(id) {
      const { data: res } = await this.$http.get('goods/' + id)
      if (res.meta.status !== 200) {
        return this.$message.error('编辑用户请求失败')
      }
      this.$message.success('编辑用户请求成功')

      this.editForm = res.data
      // console.log(this.editForm)
      this.edit_visble = true
    },
    async edit_dialog_put() {
      const { data: res } = await this.$http.put(
        'goods/' + this.editForm.goods_id,
        this.editForm
      )
      if (res.meta.status !== 200) {
        return this.$message.error('编辑用户失败')
      }
      this.$message.success('编辑用户成功')
      this.get_good_list()

      this.edit_visble = false
    },
    time_change(now) {
      const dt = new Date(now)

      const y = dt.getFullYear()
      const m = (dt.getMonth() + 1 + '').padStart(2, '0')
      const d = (dt.getDate() + '').padStart(2, '0')

      const hh = (dt.getHours() + '').padStart(2, '0')
      const mm = (dt.getMinutes() + '').padStart(2, '0')
      const ss = (dt.getSeconds() + '').padStart(2, '0')
      // yyyy-mm-dd hh:mm:ss
      return `${y}-${m}-${d} ${hh}:${mm}:${ss}`
    },
    to_link_add_good() {
      this.$router.push('/goods/add')
    }
  },
  computed: {}
}
</script>

<style scoped>
.el-input {
  width: 360px;
}
.btn-left {
  margin-left: 15px;
}
</style>
