<template>
  <div>
    <!-- 面包屑 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>商品管理</el-breadcrumb-item>
      <el-breadcrumb-item>商品列表</el-breadcrumb-item>
      <el-breadcrumb-item>添加商品</el-breadcrumb-item>
    </el-breadcrumb>
    <!-- 卡片区域 -->
    <el-card class="box-card">
      <!--  -->
      <el-alert title="添加商品信息" type="info" center show-icon> </el-alert>
      <el-steps
        :space="200"
        :active="active_index - 0"
        finish-status="success"
        align-center
      >
        <el-step title="基本信息"></el-step>
        <el-step title="商品参数"></el-step>
        <el-step title="商品属性"></el-step>
        <el-step title="商品图片"></el-step>
        <el-step title="商品内容"></el-step>
        <el-step title="完成"></el-step>
      </el-steps>
      <!-- 基本信息表单 -->
      <el-form
        ref="form"
        :model="base_form"
        label-width="80px"
        :rules="base_form_rule"
        label-position="top"
      >
        <el-tabs
          v-model="active_index"
          tab-position="left"
          :before-leave="before_leave_base"
          @tab-click="tab_click"
        >
          <el-tab-pane label="基本信息" name="0">
            <el-form-item label="商品名称" prop="goods_name">
              <el-input v-model="base_form.goods_name"></el-input>
            </el-form-item>
            <el-form-item label="商品价格" prop="goods_price">
              <el-input
                type="number"
                v-model="base_form.goods_price"
              ></el-input>
            </el-form-item>
            <el-form-item label="商品重量" prop="goods_weight">
              <el-input
                type="number"
                v-model="base_form.goods_weight"
              ></el-input>
            </el-form-item>
            <el-form-item label="商品数量" prop="goods_number">
              <el-input
                type="number"
                v-model="base_form.goods_number"
              ></el-input>
            </el-form-item>
            <el-form-item label="商品分类" prop="goods_cat">
              <el-cascader
                :options="all_CateList"
                :props="cascaderProps"
                v-model="base_form.goods_cat"
                @change="hand_change"
              ></el-cascader>
            </el-form-item>
          </el-tab-pane>
          <el-tab-pane label="商品参数" name="1">
            <!--  -->
            <el-form-item
              v-for="(tag, index) in many_list"
              :key="index"
              :label="tag.attr_name"
            >
              <!-- <p v-for="(item, index) in tag.attr_vals" :key="index">
                {{ item }}
              </p> -->
              <el-checkbox-group v-model="tag.attr_vals">
                <el-checkbox
                  v-for="(item, index) in tag.attr_vals"
                  :key="index"
                  :label="item"
                  border
                ></el-checkbox>
              </el-checkbox-group>
            </el-form-item>
          </el-tab-pane>
          <el-tab-pane label="商品属性" name="2">
            <!--  -->
            <el-form-item
              v-for="(tag, index) in only_list"
              :key="index"
              :label="tag.attr_name"
            >
              <el-input v-model="tag.attr_vals"></el-input>
            </el-form-item>
          </el-tab-pane>
          <el-tab-pane label="商品图片" name="3">
            <el-upload
              action="https://lianghj.top:8888/api/private/v1/upload"
              :on-preview="handlePreview"
              :on-remove="handleRemove"
              :on-success="handleSuccess"
              list-type="picture"
              :headers="self_header"
            >
              <el-button size="small" type="primary">点击上传</el-button>
              <div slot="tip" class="el-upload__tip">
                只能上传jpg/png文件，且不超过500kb
              </div>
            </el-upload>
          </el-tab-pane>
          <el-tab-pane label="商品内容" name="4">
            <!--  -->
            <el-button size="small" type="primary">添加商品</el-button>
          </el-tab-pane>
        </el-tabs>
      </el-form>
    </el-card>
    <el-dialog title="图片预览" width="50%" :visible.sync="preDialogVisible">
      <!-- 底部 -->
      <img :src="pre_path" alt="" class="img_pre" />
    </el-dialog>
  </div>
</template>

<script>
// #
// #region
// #endregion
export default {
  data() {
    return {
      // #
      // #region
      // checkList: [],
      // checkList: [],
      preDialogVisible: false,
      pre_path: '',
      many_list: [],
      only_list: [],
      self_header: {
        Authorization: window.sessionStorage.getItem('token')
      },
      // #endregion
      // #基本信息
      // #region
      cascader_value: [],
      cascaderProps: {
        label: 'cat_name',
        value: 'cat_id',
        children: 'children',
        expandTrigger: 'hover'
      },
      active_index: '0',
      all_CateList: [],
      base_form: {
        goods_name: '',
        goods_cat: [],
        goods_price: 0,
        goods_weight: 0,
        goods_number: 0,
        pics: []
      },
      base_form_rule: {
        goods_name: [
          { required: true, message: '请输入商品名称', trigger: 'blur' },
          { min: 1, max: 10, message: '长度在 1 到 10 个字符', trigger: 'blur' }
        ],
        goods_price: [
          { required: true, message: '请输入商品价格', trigger: 'blur' }
        ],
        goods_weight: [
          { required: true, message: '请输入商品重量', trigger: 'blur' }
        ],
        goods_number: [
          { required: true, message: '请输入商品数量', trigger: 'blur' }
        ],
        goods_cat: [{ required: true, message: '请选择商品', trigger: 'blur' }]
      }
      // #endregion
    }
  },
  methods: {
    // #
    // #region

    async get_all_CateList() {
      const { data: res } = await this.$http.get('categories')
      if (res.meta.status !== 200) {
        return this.$message.error('获取商品列表失败')
      }
      this.$message.success('获取商品列表成功')
      this.all_CateList = res.data
      // console.log(this.all_CateList)
    },
    hand_change() {
      // console.log(this.base_form.goods_cat)
      if (this.base_form.goods_cat.length !== 3) {
        this.base_form.goods_cat = []
      }
    },
    before_leave_base(to, from) {
      let flag = false
      this.$refs.form.validate((valid) => {
        flag = valid
      })
      if (flag) {
        return flag
      } else {
        this.$message.error('不合法的输入')
        return flag
      }
    },
    // #endregion
    async tab_click() {
      if (this.active_index === '1') {
        const { data: res } = await this.$http.get(
          `categories/${this.cate_id}/attributes`,
          {
            params: { sel: 'many' }
          }
        )
        if (res.meta.status !== 200) {
          return this.$message.error('获取参数失败')
        }
        this.$message.success('获取参数成功')
        // console.log(res.data)
        res.data.forEach((item) => {
          item.attr_vals =
            item.attr_vals.length === 0 ? [] : item.attr_vals.split(' ')
        })
        this.many_list = res.data
      }
      if (this.active_index === '2') {
        const { data: res } = await this.$http.get(
          `categories/${this.cate_id}/attributes`,
          {
            params: { sel: 'only' }
          }
        )
        if (res.meta.status !== 200) {
          return this.$message.error('获取属性失败')
        }
        this.$message.success('获取属性成功')
        this.only_list = res.data
        // console.log(res.data)
      }
      if (this.active_index === '3') {
      }
    },
    handlePreview(file) {
      this.pre_path = file.response.data.url
      this.preDialogVisible = true
    },
    handleRemove(file) {
      const filepath = file.response.data.tmp_path
      // console.log(filepath)
      const ix = this.base_form.pics.findIndex((x) => {
        return x.pic === filepath
      })
      // console.log(ix)
      this.base_form.pics.splice(ix, 1)
    },
    handleSuccess(res) {
      const picInfo = {
        pic: res.data.tmp_path
      }
      this.base_form.pics.push(picInfo)
      // console.log(this.base_form)
    }
  },
  computed: {
    cate_id() {
      if (this.base_form.goods_cat.length !== 3) {
        return null
      } else {
        return this.base_form.goods_cat[2]
      }
    }
  },
  created() {
    this.get_all_CateList()
  }
}
</script>

<style scoped>
.img_pre {
  width: 100%;
}
</style>
