<template>
  <div>
    <!-- 角色列表路由组件 -->
    <!-- 面包屑 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>权限管理</el-breadcrumb-item>
      <el-breadcrumb-item>角色列表</el-breadcrumb-item>
    </el-breadcrumb>
    <!-- 卡片区域 -->
    <el-card class="box-card">
      <!-- 按钮 -->
      <el-button type="primary" @click="addDialogVisible = true">
        添加角色
      </el-button>

      <!-- 角色列表渲染 -->
      <el-table border stripe :data="rolesList">
        <el-table-column type="expand">
          <template slot-scope="scope">
            <!-- {{ scope.row.children }} -->
            <el-row
              :class="['bdbottom', i1 === 0 ? 'bdtop' : '', 'vcenter']"
              v-for="(item1, i1) in scope.row.children"
              :key="item1.id"
            >
              <el-col :span="5">
                <!-- 一级 -->
                <el-tag closable @close="removeRightById(scope.row, item1.id)">
                  {{ item1.authName }}</el-tag
                >
                <i class="el-icon-caret-right"></i>
              </el-col>
              <el-col :span="19">
                <el-row
                  :class="[i2 === 0 ? '' : 'bdtop', 'vcenter']"
                  v-for="(item2, i2) in item1.children"
                  :key="item2.id"
                >
                  <el-col :span="6">
                    <!-- 二级 -->
                    <el-tag
                      type="success"
                      closable
                      @close="removeRightById(scope.row, item2.id)"
                    >
                      {{ item2.authName }}</el-tag
                    >
                    <i class="el-icon-caret-right"></i>
                  </el-col>
                  <el-col :span="18">
                    <!-- 三级 -->
                    <el-tag
                      :class="[i3 === 0 ? '' : 'bdtop']"
                      v-for="(item3, i3) in item2.children"
                      :key="item3.id"
                      type="warning"
                      closable
                      @close="removeRightById(scope.row, item3.id)"
                    >
                      {{ item3.authName }}</el-tag
                    >
                    <!-- <i class="el-icon-caret-right"></i >-->
                  </el-col>
                </el-row>
              </el-col>
            </el-row>
          </template>
        </el-table-column>
        <el-table-column label="#" type="index"> </el-table-column>
        <el-table-column label="角色名称" prop="roleName"> </el-table-column>
        <el-table-column label="角色描述" prop="roleDesc"> </el-table-column>
        <el-table-column label="操作" width="300px">
          <template slot-scope="scope">
            <el-button
              type="primary"
              icon="el-icon-edit"
              size="mini"
              @click="showEditDialog(scope.row.id)"
            >
              编辑
            </el-button>
            <el-button
              type="danger"
              icon="el-icon-delete"
              size="mini"
              @click="removeById(scope.row.id)"
            >
              删除
            </el-button>
            <el-button
              type="warning"
              icon="el-icon-setting"
              size="mini"
              @click="showSetDialogVisible(scope.row)"
            >
              分配权限
            </el-button>
            <!-- {{ scope.row }} -->
          </template>
        </el-table-column>
      </el-table>
    </el-card>

    <!-- 添加角色对话框 -->
    <el-dialog
      title="添加角色"
      width="50%"
      :visible.sync="addDialogVisible"
      @close="addDialogClosed"
    >
      <!-- 添加角色 -->
      <el-form
        label-width="80px"
        ref="addFormRef"
        :rules="editFormRules"
        :model="addForm"
      >
        <el-form-item label="角色名称" prop="roleName">
          <el-input v-model="addForm.roleName"></el-input>
        </el-form-item>
        <el-form-item label="角色描述" prop="roleDesc">
          <el-input v-model="addForm.roleDesc"></el-input>
        </el-form-item>
      </el-form>
      <!-- 底部 -->
      <span slot="footer" class="dialog-footer">
        <el-button @click="addDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="addRole"> 确 定 </el-button>
      </span>
    </el-dialog>
    <!-- 编辑角色对话框 -->
    <el-dialog
      title="编辑角色"
      width="50%"
      ref="editFormRef"
      :visible.sync="editDialogVisible"
      @close="editDialogClosed"
    >
      <!-- 修改 -->
      <el-form
        label-width="80px"
        ref="editFormRef"
        :rules="editFormRules"
        :model="editForm"
      >
        <el-form-item label="角色名称" prop="roleName">
          <el-input v-model="editForm.roleName"></el-input>
        </el-form-item>
        <el-form-item label="角色描述" prop="roleDesc">
          <el-input v-model="editForm.roleDesc"></el-input>
        </el-form-item>
      </el-form>
      <!-- 底部 -->
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="editInfo"> 确 定 </el-button>
      </span>
    </el-dialog>

    <!-- 分配权限对话框 -->
    <el-dialog
      title="分配权限"
      width="50%"
      ref="editFormRef"
      :visible.sync="setDialogVisible"
    >
      <!-- 树形 -->
      <el-tree
        :data="rightsList"
        :props="treeProps"
        show-checkbox
        node-key="id"
        default-expand-all
        :default-checked-keys="rightKeyList"
        ref="treeRef"
      ></el-tree>

      <span slot="footer" class="dialog-footer">
        <el-button @click="setDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="setRight"> 确 定 </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      // 列表
      rolesList: [],
      rightsList: [],
      treeProps: {
        label: 'authName',
        children: 'children'
      },
      rightKeyList: [],

      // 对话框控制
      addDialogVisible: false,
      editDialogVisible: false,
      setDialogVisible: false,

      // 表单
      addForm: {
        roleName: '',
        roleDesc: ''
      },
      editForm: {
        roleName: '',
        roleDesc: ''
      },
      roleId: '',
      // 验证规则
      editFormRules: {
        roleName: [
          { required: true, message: '请输角色名称', trigger: 'blur' },
          { min: 3, max: 10, message: '长度在 3 到 10 个字符', trigger: 'blur' }
        ],
        roleDesc: [
          { required: true, message: '请输入角色描述', trigger: 'blur' },
          { min: 5, max: 20, message: '长度在 5 到 20 个字符', trigger: 'blur' }
        ]
      },
      editFormRules2: {
        roleName: [
          { required: true, message: '请输角色名称', trigger: 'blur' },
          { min: 3, max: 10, message: '长度在 3 到 10 个字符', trigger: 'blur' }
        ],
        roleDesc: [
          { required: true, message: '请输入角色描述', trigger: 'blur' },
          { min: 5, max: 20, message: '长度在 5 到 20 个字符', trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    // 获取角色列表
    async getRolesList() {
      // 获取数据
      const { data: res } = await this.$http.get('roles')
      // 验证
      if (res.meta.status !== 200) {
        return this.$message.error('获取角色列表失败')
      }
      // 同步数据
      this.rolesList = res.data
      this.$message.success('获取角色列表成功')
      //   console.log(this.rolesList) // 测试数据
    },

    // 添加角色函数
    addRole() {
      this.$refs.addFormRef.validate(async (valid) => {
        // 输入验证
        if (!valid) {
          return this.$message.error('输入失败')
        }

        // 发起请求
        const { data: res } = await this.$http.post('roles', this.addForm)

        // 验证请求
        if (res.meta.status !== 201) {
          return this.$message.error('添加失败')
        }

        // 收尾
        this.$message.success(res.meta.msg)
        this.addDialogVisible = false // 关闭窗口
        this.getRolesList()
      })
    },

    // 关闭时候表单重置
    addDialogClosed() {
      this.$refs.addFormRef.resetFields()
    },
    editDialogClosed() {
      this.$refs.editFormRef.resetFields()
    },

    async showEditDialog(id) {
      const { data: res } = await this.$http.get('roles/' + id)
      if (res.meta.status !== 200) {
        return this.$message.error('编辑请求失败')
      }
      this.$message.success('编辑请求成功')
      this.editForm = res.data
      this.editDialogVisible = true
    },

    // 编辑请求
    editInfo() {
      // 预验证

      this.$refs.editFormRef.validate(async (valid) => {
        // 预验证失败跳出
        if (!valid) {
          return this.$message.error('校验数据不通过')
        }
        // 发起修改数据请求
        const { data: res } = await this.$http.put(
          'roles/' + this.editForm.roleId,
          this.editForm
        )
        // 更新失败
        if (res.meta.status !== 200) {
          return this.$message.error('更新失败')
        }
        // 关闭对话框
        this.editDialogVisible = false
        // 刷新列表
        this.getRolesList()
        // 提示
        this.$message.success(res.meta.msg)
      })
    },

    // 移除
    async removeById(id) {
      // 弹窗提示
      const confirmResult = await this.$confirm(
        '此操作将永久删除, 是否继续?',
        '警告',
        {
          cancelButtonText: '取消',
          confirmButtonText: '确定',
          type: 'warning'
        }
      ).catch((err) => {
        return err
      })
      // 用户确认删除为 confirm
      //   console.log(confirmResult)
      if (confirmResult !== 'confirm') {
        return this.$message.info('取消删除')
      }
      // 删除操作
      const { data: res } = await this.$http.delete('roles/' + id)
      if (res.meta.status !== 200) {
        return this.$message.error('删除失败')
      }
      // 确定删除
      this.$message.success(res.meta.msg)

      // 刷新列表
      this.getRolesList()
    },
    // 移除权限
    async removeRightById(role, id) {
      // 弹窗提示
      const confirmResult = await this.$confirm(
        '此操作将永久删除, 是否继续?',
        '警告',
        {
          cancelButtonText: '取消',
          confirmButtonText: '确定',
          type: 'warning'
        }
      ).catch((err) => {
        return err
      })
      // 用户确认删除为 confirm
      if (confirmResult !== 'confirm') {
        return this.$message.info('取消删除')
      }
      const { data: res } = await this.$http.delete(
        'roles/' + role.id + '/rights/' + id
      )
      if (res.meta.status !== 200) {
        return this.$message.error('删除失败')
      }
      this.$message.success(res.meta.msg)

      // 刷新列表
      role.children = res.data
    },

    // 设置权限
    async showSetDialogVisible(role) {
      this.roleId = role.id
      const { data: res } = await this.$http.get('rights/tree')
      if (res.meta.status !== 200) {
        return this.$message.error('权限请求失败')
      }
      this.$message.success('权限请求成功')
      this.rightsList = res.data
      this.setDialogVisible = true
      //   console.log(role)
      const idList = this.getIdList(role, [])
      //   console.log(idList)
      this.rightKeyList = idList
      //   this.rightsList = idList
      //   console.log(this.rightsList)
    },

    // 递归获得id
    getIdList(obj, idList) {
      if (obj.children === undefined) {
        // console.log(obj.id)
        return idList.push(obj.id)
      } else {
        obj.children.forEach((element) => {
          this.getIdList(element, idList)
        })
      }
      return idList
    },

    // 分配权限
    async setRight() {
      const keyList = [
        ...this.$refs.treeRef.getCheckedKeys(),
        ...this.$refs.treeRef.getHalfCheckedKeys()
      ]
      const keyStr = keyList.join(',')
      const { data: res } = await this.$http.post(
        'roles/' + this.roleId + '/rights',
        {
          rids: keyStr
        }
      )
      if (res.meta.status !== 200) {
        return this.$message.error('更新失败')
      }
      this.$message.success('更新成功')
      this.getRolesList()
      this.setDialogVisible = false
    }
  },
  created() {
    // 加载时获取
    this.getRolesList()
  }
}
</script>

<style scoped>
.el-tag {
  margin: 7px;
}

.bdtop {
  border-top: 1px solid #eee;
}

.bdbottom {
  border-bottom: 1px solid #eee;
}
.vcenter {
  display: flex;
  align-items: center;
}
</style>
