<template>
  <div>
    <!-- 面包屑 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>商品管理</el-breadcrumb-item>
      <el-breadcrumb-item>商品分类</el-breadcrumb-item>
    </el-breadcrumb>
    <!-- 卡片区域 -->
    <el-card class="box-card">
      <!-- 头部 -->
      <el-row>
        <el-col>
          <el-button type="primary" @click="openAddCate">添加分类</el-button>
        </el-col>
      </el-row>
      <!-- 表格 -->
      <tree-table
        :data="cateList"
        :columns="columns"
        :selection-type="false"
        :expand-type="false"
        show-index
        index-text="#"
        border
        max-height="5"
      >
        <!--isOk  -->
        <template slot="isOk" slot-scope="scope">
          <i
            v-if="scope.row.cat_deleted === false"
            class="el-icon-success"
            style="color: lightgreen"
          ></i>
          <i
            v-if="scope.row.cat_deleted === true"
            class="el-icon-error"
            style="color: red"
          ></i>
        </template>
        <!-- order -->
        <template slot="order" slot-scope="scope">
          <el-tag v-if="scope.row.cat_level === 0">一级</el-tag>
          <el-tag type="success" v-if="scope.row.cat_level === 1">二级</el-tag>
          <el-tag type="warning" v-if="scope.row.cat_level === 2">三级</el-tag>
        </template>
        <!-- opt -->
        <template slot="opt" slot-scope="scope">
          <el-button
            type="primary"
            icon="el-icon-edit"
            size="mini"
            @click="openEditCate(scope.row)"
          >
            编辑
          </el-button>
          <el-button
            type="danger"
            icon="el-icon-delete"
            size="mini"
            @click="del_Cate(scope.row)"
          >
            删除
          </el-button>
        </template>
      </tree-table>
      <!-- 分页 -->
      <el-pagination
        :page-sizes="[3, 5, 10, 15]"
        layout="total, sizes, prev, pager, next, jumper"
        :total="this.total"
        :page-size="this.queryInfo.pagesize"
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        @prev-click="prevClick"
        @next-click="nextClick"
      >
      </el-pagination>
    </el-card>
    <!-- 对话框 -->
    <el-dialog
      title="添加商品"
      width="50%"
      :visible.sync="showAddCateDialog"
      @close="CloseAddForm"
    >
      <!-- 添加分类 -->
      <el-form
        label-width="80px"
        :model="addForm"
        :rules="addFormRule"
        ref="addFormRef"
      >
        <el-form-item label="分类名称" prop="cat_name">
          <el-input v-model="addForm.cat_name"></el-input>
        </el-form-item>
        <el-form-item label="父级分类">
          <el-cascader
            :options="ParentCateList"
            :props="cascaderProps"
            @change="ParentCateChange"
            clearable
          ></el-cascader>
        </el-form-item>
      </el-form>
      <!-- 底部 -->
      <span slot="footer" class="dialog-footer">
        <el-button @click="showAddCateDialog = false">取 消</el-button>

        <el-button type="primary" @click="PostAddForm"> 确 定 </el-button>
      </span>
    </el-dialog>

    <el-dialog
      title="修改分类"
      width="50%"
      :visible.sync="showEditCateDialog"
      @close="CloseEditForm"
    >
      <!-- 添加分类 -->
      <el-form
        label-width="80px"
        :model="EditForm"
        :rules="EditFormRule"
        ref="EditFormRef"
      >
        <el-form-item label="分类名称" prop="cat_name">
          <el-input v-model="EditForm.cat_name"> </el-input>
        </el-form-item>
      </el-form>
      <!-- 底部 -->
      <span slot="footer" class="dialog-footer">
        <el-button @click="showEditCateDialog = false">取 消</el-button>
        <el-button type="primary" @click="PostEditForm"> 确 定 </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      // 删除模块
      // #region

      // #endregion

      // 商品分类列表
      cateList: [],
      ParentCateList: [],
      // 查询条件
      queryInfo: {
        type: 3,
        pagenum: 1,
        pagesize: 5
      },
      // 总数
      total: 0,
      // columns
      columns: [
        { label: '分类名称', prop: 'cat_name' },
        { label: '是否有效', type: 'template', template: 'isOk' },
        { label: '排序', type: 'template', template: 'order' },
        { label: '操作', type: 'template', template: 'opt' }
      ],
      // 对话框控制
      showAddCateDialog: false,

      // 验证规则
      addFormRule: {
        cat_name: [
          { required: true, message: '请输入分类名', trigger: 'blur' },
          {
            min: 1,
            max: 100,
            message: '长度在 3 到 10 个字符',
            trigger: 'blur'
          }
        ]
      },
      addForm: {
        cat_pid: 0,

        cat_name: '',
        cat_level: 0
      },
      cascaderProps: {
        label: 'cat_name',
        value: 'cat_id',
        children: 'children',
        expandTrigger: 'hover',
        checkStrictly: true
      },
      selectKey: [],
      // 编辑分类模块变量
      showEditCateDialog: false,
      EditForm: {
        cat_name: '',
        cat_id: 0
      },
      EditFormRule: {
        cat_name: [
          { required: true, message: '请输入要修改的信息', trigger: 'blur' },
          {
            min: 1,
            max: 10,
            message: '长度在 1 到 10 个字符',
            trigger: 'blur'
          }
        ]
      }
    }
  },
  created() {
    this.getCateList()
    // console.log('musaly')
  },
  methods: {
    // 获取数据
    async getCateList() {
      // 获取数据
      const { data: res } = await this.$http.get('categories', {
        params: this.queryInfo
      })
      // 验证
      if (res.meta.status !== 200) {
        return this.$message.error('获取失败')
      }
      // console.log(res.data[1]) // 测试
      // console.log(res)
      // 同步数据
      this.$message.success('获取成功')
      this.cateList = res.data.result
      this.total = res.data.total
    },
    // 分页功能
    async handleSizeChange(value) {
      this.queryInfo.pagesize = value
      this.getCateList()
    },
    async handleCurrentChange(value) {
      this.queryInfo.pagenum = value
      this.getCateList()
    },
    async prevClick(value) {
      this.queryInfo.pagenum = value - 1
      this.getCateList()
    },
    async nextClick(value) {
      this.queryInfo.pagenum = value + 1
      this.getCateList()
    },

    // 添加分类模块
    // #region
    openAddCate() {
      this.getAddCateList()
      this.showAddCateDialog = true
    },
    async getAddCateList() {
      const { data: res } = await this.$http.get('categories', {
        params: { type: 2 }
      })
      // 验证
      if (res.meta.status !== 200) {
        return this.$message.error('获取失败')
      }
      this.ParentCateList = res.data
    },
    ParentCateChange() {
      console.log(this.selectKey)
      if (this.selectKey.length > 0) {
        this.addForm.cat_pid = this.selectKey[this.selectKey.length - 1]
        this.addForm.cat_level = this.selectKey.length
        return 0
      } else {
        this.addForm.cat_pid = 0
        this.addForm.cat_level = 0
      }
      console.log(this.addForm)
    },
    CloseAddForm() {
      this.$refs.addFormRef.resetFields()
      this.selectKey = []
      this.addForm.cat_pid = 0
      this.addForm.cat_level = 0
    },
    PostAddForm() {
      this.$refs.addFormRef.validate(async (valid) => {
        // console.log(this.addForm)
        if (!valid) {
          return this.$message.error('输入失败')
        }
        const { data: res } = await this.$http.post('categories', this.addForm)
        if (res.meta.status !== 201) {
          return this.$message.error('添加失败')
        }
        this.$message.success('添加成功')

        this.getCateList()
        this.showAddCateDialog = false
      })
    },
    // #endregion

    // 编辑分类模块
    // #region
    openEditCate(target) {
      this.EditForm.cat_name = target.cat_name
      this.EditForm.cat_id = target.cat_id
      this.showEditCateDialog = true
    },
    async PostEditForm() {
      const { data: res } = await this.$http.put(
        'categories/' + this.EditForm.cat_id,
        {
          cat_name: this.EditForm.cat_name
        }
      )
      // 验证
      if (res.meta.status !== 200) {
        return this.$message.error('获取失败')
      }
      this.getCateList() // 页面完整渲染
      this.$message.success(res.meta.msg)
      this.showEditCateDialog = false
    },
    CloseEditForm() {
      this.$refs.EditFormRef.resetFields()
    },
    // #endregion

    // 删除分类模块
    // #region
    async del_Cate(target) {
      const confirmResult = await this.$confirm(
        '此操作将永久删除, 是否继续?',
        '警告',
        {
          cancelButtonText: '取消',
          confirmButtonText: '确定',
          type: 'warning'
        }
      ).catch((err) => {
        return err
      })

      if (confirmResult !== 'confirm') {
        return this.$message.info('取消删除')
      }
      const { data: res } = await this.$http.delete(
        'categories/' + target.cat_id
      )
      if (res.meta.status !== 200) {
        return this.$message.error('删除失败')
      }
      this.$message.success(res.meta.msg)
      this.getCateList() // 页面完整渲染
    }

    // #endregion
  }
}
</script>

<style scoped>
.el-row {
  margin-bottom: 15px;
}
.el-cascader {
  width: 100%;
}
</style>
